import React from 'react'

import {Link} from 'react-router-dom';

function FilterpageCard(props) {
  const {product,id}=props;
  return (
   
    <div key={props.id}>
      
      <Link to={"/Detail/"+id}  style={{color:"#000000"}}>
       <div className="card">
       <img src={props.img} alt="Avatar" height="120px" width="120px" />
        <div className="container">
           <h4><b>{props.food}</b></h4>
          <p>{props.name}</p>
          <div style={{display: "flex",flexDirection: "row"}}>

          <h4 style={{padding:"10px"}}>Rating:{props.restoRating}<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-star-fill" viewBox="0 0 20 20">
  <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.283.95l-3.523 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z"/>
</svg></h4>

         <h5 style={{marginLeft:"100px"}}> Price: â‚¹{props.price}</h5>
         </div>
        </div>
      </div>
      </Link>
    
    </div>
   
  )
}

export default FilterpageCard
